.container {
  @include flex-box;
}
.recipe-container {
  @include flex-column;
  justify-content: space-around;
}

.recipe {
  @include flex-box;
  border: solid 2px black;
}
.single-recipe {
  @include flex-column;
  width: 80vw;
  margin-top: 1rem;

  p {
    @include flex-row;
    margin: 0.5rem;
  }
  .ing {
    @include flex-column;
  }

  h2 {
    font-size: 5rem;
  }
  h3 {
    font-size: 1rem;
    color: $onn1;
  }
}

ul {
  // display: grid;
  // grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  list-style: none;
  li {
    i {
      font-size: 2rem;
      color: $onn4;
    }
    img {
      width: 2rem;
    }
  }
}

.linkP {
  margin: 1rem;
  padding: 0.5rem;
  a {
    color: black;
  }
  border-radius: 0.5rem;
  background-color: $onn2;
  &:hover {
    cursor: pointer;
  }
}

@media (max-width: 500px) {
  .recipe-container {
    h2 {
      font-size: 2.4rem;
    }
  }
  .single-recipe {
    @include flex-column;
    width: 80vw;
    margin-top: 1rem;
    img {
      width: 80vw;
    }
    .twoBlocks {
      @include flex-column;
      .leftBlock {
        width: 100vw;
      }
      .rightBlock {
        margin-top: 1rem;
        margin-bottom: 3rem;
        width: 80vw;
      }
    }
  }
  .linkP {
    margin-top: 3rem;
  }
}
